<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
    <Pano title="jia" width="720" height="480" bundle="static/pano/" format="jpg"></Pano>
  </div>
</template>

<script>
import Pano from 'vue-pano'
export default {
  name: 'App',
  components: { Pano }
}
</script>

<style>
* { touch-action: pan-y; } 
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



